//External Lib Import
import { createSlice } from "@reduxjs/toolkit";

const ProfileSlice = createSlice({
  name: "Profile",
  initialState: {
    ProfileLists: [],
    ProfileDropDown: [],
    TotalProfile: 0,
    DepartmentHeadsList: [],
    StaffList: [],
    ProfileDetails: {
      FirstName: "",
      LastName: "",
      Gender: "",
      DateOfBirth: "",
      Address: "",
      Phone: "",
      Email: "",
      Image:
        "data:image/png;base64,",
    },
  },
  reducers: {
    SetProfileLists(state, action) {
      state.ProfileLists = action.payload;
    },
    SetTotalProfile(state, action) {
      state.TotalProfile = action.payload;
    },
    SetProfileDropDown(state, action) {
      state.ProfileDropDown = action.payload;
    },
    SetProfileDetails(state, action) {
      state.ProfileDetails = action.payload;
    },
    SetDepartmentHeadsList(state, action) {
      state.DepartmentHeadsList = action.payload;
    },
    SetStaffList(state, action) {
      state.StaffList = action.payload;
    },
    ResetProfileDetails(state, action) {
      state.FirstName = "";
      state.LastName = "";
      state.Gender = "";
      state.DateOfBirth = "";
      state.Address = "";
      state.Phone = "";
      state.Email = "";
      state.Image =
        "data:image/png;base64,";
    },
  },
});

export const {
  SetProfileLists,
  SetTotalProfile,
  SetProfileDropDown,
  SetProfileDetails,
  ResetProfileDetails,
  SetDepartmentHeadsList,
  SetStaffList,
} = ProfileSlice.actions;
export default ProfileSlice.reducer;
